*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
body{
  background-color: rgb(255, 255, 255);
  overflow-x: hidden;
  position: relative;
  
}
html {
  scroll-behavior: smooth;
  scroll-padding-top: 75px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: normal;
}

p,
a,
li,
button,
ul {
  margin: 0;
  padding: 0;
  line-height: normal;
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}
/*Inicio*/
