/*proyectos*/
.projects{
  display: grid;
  grid-template-columns: repeat(3, 400px);
  gap: 50px;
  justify-content: center;
  align-content: center;
  margin: 30px 0;
}
.proj-imgbx {
  
  color: white;
  width: 400px;
  height: 400px;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  
}
.proj-imgbx img{ 
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.proj-imgbx::before {
  content: "";
  background: linear-gradient(90.21deg, #1f1a1ab9 -5.91%, #000000d0
  111.58%);
  opacity: 0.85;
  position: absolute;
  width: 100%;
  height: 0;
  transition: 0.4s ease-in-out;
}
.proj-imgbx:hover::before {
  height: 100%;
}
.proj-txtx {
  
  font-weight: 100;
  position: absolute;
  text-align: center;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s ease-in-out;
  opacity: 0;
  width: 80%;
}
.proj-imgbx:hover .proj-txtx {
  top: 50%;
  opacity: 1;
}
.proj-txtx h4 {
  font-family: StingerF;
  font-size: 27px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 1.1em;
}
.proj-txtx span {
  font-style: italic;
  font-weight: 400;
  font-size: 12px;
  letter-spacing: 0.8px;

}
